<template>
  <div class="wrapper">
    <Set />
    <div class="info">
      <div class="info__top">
        <img class="info__top__img" src="../../assets/myPage/boy.jpg" alt="" >
        <div class="info__top__username">
          <span class="info__details"></span>
          <span class="info__icon iconfont">&#xe622;</span>
        </div>
        <div class="info__top__id">IDzaiwlw</div>
      </div>
      <div class="info__bottom">
        <div class="info__item" v-for="(item,key) in infoBottomItemList" :key="key">
          <div class="info__item__count">{{item.count}}</div>
          <div class="info__item__name">{{item.name}}</div>
        </div>
      </div>
    </div>
    <Notice />
    <Options :optionList="optionList"/>
    <Options :optionList="optionList2"/>
  </div>
  <Docker whichPage=""/>
</template>

<script>
import { reactive, toRefs } from 'vue'
import Docker from '../../components/Docker'
import { get } from '../../utils/request'
import Notice from './Notice'
import Set from './Set'
import Options from './Options'

const infoBottomItem = {
  item1: { name: '', count: 218 },
  item2: { name: '', count: 12 }
}
const optionList = [
  { icon: '&#xe6f0;', name: '', right: '&#xe622;' },
  { icon: '&#xe694;', name: '', right: '&#xe622;' },
  { icon: '&#xe623;', name: '', right: '&#xe622;' },
  { icon: '&#xe60a;', name: '', right: '&#xe622;' },
  { icon: '&#xe8ad;', name: '', right: '&#xe622;' },
  { icon: '&#xe640;', name: '', right: '&#xe622;' },
  { icon: '&#xe614;', name: '', right: '&#xe622;' }
]
const optionList2 = [
  { icon: '&#xe695;', name: '', right: '&#xe622;' },
  { icon: '&#xe639;', name: '', right: '&#xe622;' },
  { icon: '&#xe618;', name: '', right: '&#xe622;' },
  { icon: '&#xe632;', name: '', right: '&#xe622;' },
  { icon: '&#xe617;', name: '', right: '&#xe622;' }
]
const useGetMyPageInfoEffet = () => {
  const data = reactive({ info: {} })
  const getMyPageInfo = async () => {
    const result = await get('/api/user/info')
    if (result?.errno === 0 && result?.data) {
      data.info = result?.data
    }
  }
  getMyPageInfo()
  const { info } = toRefs(data)
  return { info }
}

export default {
  name: 'MyPage',
  components: { Docker, Notice, Options, Set },
  setup () {
    const { info } = useGetMyPageInfoEffet()
    const infoBottomItemList = infoBottomItem
    return { info, infoBottomItemList, optionList, optionList2 }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mixins.scss';
@import '../../style/variables.scss';
.wrapper{
  @include wrapperStyle;
}
.info{
  position: relative;
  margin: .6rem .16rem .16rem .16rem;
  background: $bgColor;
  box-shadow: 0 .08rem .16rem 0 rgba(0,0,0,0.08);
  border-radius: .08rem;
  &__top{
    &__img{
      border-radius: 50%;
      width: .6rem;
      height: .6rem;
      position: absolute;
      top: .2rem;
      left: .2rem;
    }
    &__username{
      font-family: PingFangSC-Regular;
      font-size: .24rem;
      color: #262628;
      letter-spacing: .0019rem;
      padding: .2rem .2rem 0 1rem;
    }
    &__id{
      font-family: PingFangSC-Regular;
      font-size: .14rem;
      color: #C1C0C9;
      margin: 0 0 .4rem 1rem;
    }
  }
  &__bottom{
    display: flex;
    border-top: .01rem solid $content-bgColor;
    padding: .12rem 0 .16rem 0;
    .info__item{
      flex: 1;
      &__name{
        font-family: PingFangSC-Regular;
        font-size: .12rem;
        line-height: .17rem;
        color: #C1C0C9;
        text-align: center;
        margin: .1rem 0 .03rem 0;
      }
      &__count{
        font-family: PingFangSC-Regular;
        font-size: .3rem;
        line-height: .28rem;
        color: #99ddff;
        text-align: center;
      }
    }
  }
  &__details{
    margin-left: 1rem;
    font-size: .12rem;
    color: #aaa;
  }
  &__icon{
    font-size: .12rem;
  }
}

</style>
